import headers from "../../components/header_nav.vue"
export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "申请成为商家",
			Hlicon: "../../static/icon/left_whitch.png",
			showtype: false,
			typename: "请选择店铺类别",
			type: '', //店铺类别
			name: '', //
			idcard: '', //身份证号
			mobile: '', //手机号码
			frontimg: '', //正面图片
			reverimg: '', //反面图片
			merchname: '', //店铺名称
			address: '', //店铺地址
			merch_mobile: '', //店铺联系电话
			businessImg: '', //营业执照
			flatImg: '', //展示图
			bankrealname: '', //银行实名
			bankname: '', //银行
			bankcard: '', //银行卡号
			longitude: '', //经度
			latitude: '', //纬度
			statu: uni.getStorageSync('merchantAuth'), //店铺申请的状态值
			//referrer:'',//推荐人IDd
			droplis: uni.getStorageSync('category') /* [
				{
					id: 1,
					name: "洗车服务"
				},
				{
					id: 2,
					name: "修车服务"
				},
				{
					id: 3,
					name: "年审服务"
				},
				{
					id: 4,
					name: "学车服务"
				},
				{
					id: 5,
					name: "加油服务"
				}
			] */
		}
	},
	methods: {
		upLocadfile(_success) {
			var self = this;
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: self.$service.api.home.uploadImgHLF, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						// formData: {
						//     'user': 'test'
						// },
						success: (uploadFileRes) => {
							var ss = JSON.parse(uploadFileRes.data);
							console.log(ss);
							_success(self, ss);
							// e = ss.data.url;
						}
					});
				}
			});
		},

		front(i) {
			if (i == 1) {
				this.upLocadfile(function(self, res) {
					self.frontimg = res.data.url;
					uni.showToast({
						title: res.info,
						icon: "none"
					})
				});
			} else if (i == 2) {
				this.upLocadfile(function(self, res) {
					self.reverimg = res.data.url;
					uni.showToast({
						title: res.info,
						icon: "none"
					})
				});
			} else if (i == 3) {
				this.upLocadfile(function(self, res) {
					self.businessImg = res.data.url;
					uni.showToast({
						title: res.info,
						icon: "none"
					})
				});
			} else if (i == 4) {
				this.upLocadfile(function(self, res) {
					self.flatImg = res.data.url;
					uni.showToast({
						title: res.info,
						icon: "none"
					})
				});
			}

			// this.upLocadfile(function(self , res){
			// 	self.frontimg = res.data.url;
			// 	uni.showToast({
			// 		title:res.info,
			// 		icon:"none"
			// 	})
			// });
		},

		/*-------*/
		close(i) {
			if (i == 1) {
				this.frontimg = '';
			} else if (i == 2) {
				this.reverimg = '';
			} else if (i == 3) {
				this.businessImg = '';
			} else if (i == 4) {
				this.flatImg = '';
			}
		},
		/*-------*/

		seltype() {
			this.showtype = !this.showtype
		},
		selservice(e, n) {
			this.showtype = !this.showtype;
			this.typename = n;
			this.type = e;
		},

		getLoc() {
			// uni.getLocation({
			//     type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			//     success: function (res) {
			//         const latitude = res.latitude;
			//         const longitude = res.longitude;
			//         uni.openLocation({
			//             latitude: latitude,
			//             longitude: longitude,
			//             success: function (res) {
			//                 console.log('success',res);
			//             }
			//         });
			//     }
			// });
			// var self = thisfunction;
			uni.chooseLocation({
				success: (res)=>{
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					this.latitude = res.latitude;
					this.longitude = res.longitude;
					this.address = res.address;
				}
			});
		},


		submit() {
			var self = this;
			uni.showModal({
				title: '提示',
				content: '请确保所有项目已填写和正确,否则可能会导致申请不通过!',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						self.$service.auth(self, self.$service.api.center.apply_merch, 'POST', {
							type: self.type,
							name: self.name,
							idcard: self.idcard,
							mobile: self.mobile,
							identity_positive_image: self.frontimg,
							identity_other_image: self.reverimg,
							merchname: self.merchname,
							merch_address: self.address,
							merch_mobile: self.merch_mobile,
							merch_license: self.businessImg,
							merch_showimg: self.flatImg,
							bankrealname: self.bankrealname,
							bankname: self.bankname,
							bankcard: self.bankcard,
							lat: self.longitude,
							lng: self.latitude

						}, function(self, res) {
							console.log(res);
							uni.showToast({
								title: res.info,
								icon: "none"
							});
							

							setTimeout(() => {
								self.statu = 2;
								uni.switchTab({
									url: '/pages/main/my'
								})
							}, 1500)

						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});


		}
	},
	onShow() {
		// uni.getLocation({
		//     type: 'wgs84',
		//     success:(res)=>{
		// 		this.longitude = res.longitude;
		// 		this.latitude = res.latitude;
		//         console.log('当前位置的经度：' + res.longitude ,"longitude");
		//         console.log('当前位置的纬度：' + res.latitude);
		//     }
		// });
		// this.$service.auth(this , this.$service.api.center.check_status, 'POST' , '' , function(self , res){
		// 	console.log(res)
		// })
		
		

		uni.request({//获取商家认证状态
			url: this.$service.api.center.check_status,
			method: 'POST',
			header: {
				Authorization: 'Bearer ' + uni.getStorageSync('token')
			},
			success: (res) => {
				var res = res.data
				this.statu = res.status;
				uni.setStorageSync('merchantAuth' , this.statu);
				console.log(this.statu , 'merchantAuth');
			}
		});
	}

}
